<template>
    <div class="container-fluid">
        <div id="wrapper">
            <div id="menu">
                <p class="welcome">{{recipient}}<b></b></p>
                <p class="logout" >
                    <a id="exit" v-on:click="$router.go(-1)" href="#">Exit Chat</a>
                </p>
            </div>

            <div id="chatbox">
                <ul id="chatBoxMessages">

                </ul>
            </div>

            <div class="formInput">
                <input name="usermsg" type="text" id="usermsg" />
                <div class="btn btn-submit" name="submitmsg" type="submit" id="submitmsg" value="Send" v-on:click="sendMessage()">Send</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            currentUser: String,
            recipient: String
        },
        data() {
            return {
                recipients: this.recipient,
                currentUserCredentials: this.currentUser
            }            
        },
        methods: {
            sendMessage() {
                this.$store.state.signalr.connection.invoke("SendMessage", this.currentUserCredentials, this.recipients, document.getElementById('usermsg').value).catch(function (err) {
                    return console.error(err);
                });
            }
        }
    }
</script>

<style scoped>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        margin: 20px auto;
        font-family: "Lato";
        font-weight: 300;
    }

    #formInput {
        padding: 15px 25px;
        display: flex;
        gap: 10px;
        justify-content: center;
    }

        #formInput label {
            font-size: 1.5rem;
            font-weight: bold;
        }

    div.btn {
        font-family: "Lato";
    }

    a {
        color: #0000ff;
        text-decoration: none;
    }

        a:hover {
            text-decoration: underline;
        }

    #wrapper,
    #loginform {
        margin: 0 auto;
        padding-bottom: 25px;
        background: #eee;
        width: 600px;
        max-width: 100%;
        border: 2px solid #212121;
        border-radius: 4px;
    }

    #loginform {
        padding-top: 18px;
        text-align: center;
    }

        #loginform p {
            padding: 15px 25px;
            font-size: 1.4rem;
            font-weight: bold;
        }

    #chatbox {
        text-align: left;
        margin: 0 auto;
        margin-bottom: 25px;
        padding: 10px;
        background: #fff;
        height: 300px;
        width: 530px;
        border: 1px solid #a7a7a7;
        overflow: auto;
        border-radius: 4px;
        border-bottom: 4px solid #a7a7a7;
    }

    #usermsg {
        flex: 1;
        border-radius: 4px;
        border: 1px solid #ff9800;
        padding-left: 5px;
    }

    #name {
        border-radius: 4px;
        border: 1px solid #ff9800;
        padding: 2px 8px;
    }

    #submitmsg,
    #enter {
        background: #ff9800;
        border: 2px solid #e65100;
        color: white;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer !important;
        margin-left: 10px;
    }

    .error {
        color: #ff0000;
    }

    #menu {
        padding: 15px 25px;
        display: flex;
    }

        #menu p.welcome {
            flex: 1;
        }

    a#exit {
        color: white;
        background: #c62828;
        padding: 4px 8px;
        border-radius: 4px;
        font-weight: bold;
    }

    .msgln {
        margin: 0 0 5px 0;
    }

        .msgln span.left-info {
            color: orangered;
        }

        .msgln span.chat-time {
            color: #666;
            font-size: 60%;
            vertical-align: super;
        }

        .msgln b.user-name, .msgln b.user-name-left {
            font-weight: bold;
